import React, { useState, useEffect,useCallback } from "react";
import { Table, Button, Popconfirm } from "antd";
import { EditOutlined, ClearOutlined } from "@ant-design/icons";
import type { ColumnsType } from "antd/es/table";
import Dialog from "../components/ProductDirection/ProductDirectionDialog";
import { useDispatch, useSelector } from "react-redux";
import {
  asyncAddList as addList,
  asyncDelList as delList,
  asyncInitList as init,
  initPayload,
} from "../store/infoReducer";
interface DataType {
  _id: string;
  productdirection: string;
}

const Product = () => {
  const columns: ColumnsType<DataType> = [
    {
      title: "出线方向",
      dataIndex: "productdirection",
      width: "50%",
    },
    {
      title: "状态",
      dataIndex: "status",
      width: "50%",
      render: (_, record) => (
        <Popconfirm
          title="你确定要删除吗?"
          onConfirm={() => {
            delItem(record._id);
          }}
          okText="确定"
          cancelText="取消"
        >
          <Button
            type="primary"
            style={{ backgroundColor: "#e2a241", border: "#edc523" }}
            icon={<ClearOutlined />}
          >
            删除
          </Button>
        </Popconfirm>
      ),
    },
  ];

  let [isShow, setIsshow] = useState(false);
  let showDialog = (val: boolean) => {
    setIsshow(val);
  };
  let dispatch = useDispatch();
  let getList = useCallback(() => {
    dispatch(init(initPayload.productDirectionList) as any);
  }, [dispatch]);
  useEffect(() => {
    getList()
  }, [getList]);

  let list = useSelector((state: any) => state.info.productDirectionList);
  let addItem = (val: object) => {
    dispatch(addList(val, initPayload.productDirectionList) as any);
  };
  let delItem = (_id: string) => {
    dispatch(delList(_id, initPayload.productDirectionList) as any);
  };
  return (
    <div style={{maxHeight:"100%",overflowY:"auto"}}>
      <Table
        columns={columns}
        dataSource={list}
        rowKey="_id"
        bordered
        pagination={false}
        title={() => (
          <Button
            type="primary"
            onClick={() => {
              showDialog(true);
            }}
            icon={<EditOutlined />}
          >
            新增方向
          </Button>
        )}
        // footer={() => "Footer"}
      />
      <Dialog
        isShow={isShow}
        showDialog={showDialog}
        addItem={addItem}
      ></Dialog>
    </div>
  );
};

export default Product;
