import { Modal, Form, Input, } from "antd";
import React from "react";

const ProductDirectionDialog = ({ isShow, showDialog,addItem }: any) => {
  const [form] = Form.useForm();

  const handleOk = () => {
    form.submit();
  };

  const handleCancel = () => {
    showDialog(false);
    form.resetFields();
  };

  const onFinish = (values: any) => {
    addItem(values)
    showDialog(false);
    form.resetFields()
  };

  return (
    <Modal
      title="新增产品出线方向"
      open={isShow}
      onOk={handleOk}
      onCancel={handleCancel}
    >
      <Form
        name="basic"
        form={form}
        labelCol={{ span: 4 }}
        wrapperCol={{ span: 20 }}
        initialValues={{ remember: true }}
        onFinish={onFinish}
        autoComplete="off"
      >
        <Form.Item
          label="出线方向"
          name="productdirection"
          rules={[{ required: true, message: "请输入产品出线方向" }]}
        >
          <Input />
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default ProductDirectionDialog;
